.product-container {
    padding: 30px 20px 100px;
    margin: 0 auto;
    max-width: 1400px;
    min-height: 300px;
    width: 100%;
    height: auto;

    display: flex;
    justify-content: center;
    align-items: flex-start; // 避免浮動

    @include media768 {
        padding: 50px 15px 10px;
    }
    @include media568 {
        padding: 10px;
    }
    @include media375 {
        padding: 5px;
    }

    // 右邊主要區塊 ( 廣告欄、卡片、導航、頁數 )
    .product-content-container {
        width: 80%;
        // height: 100%;
        height: 100%;
        min-height: 500px; // 固定高度，不因無商品數量導致水平差異
        padding: 10px 10px;
        // background-color: burlywood;

        @include media568 {
            width: 100%;
        }
    }
}
