.shopping-navbar {
    width: 100%;
    // padding: 50px 20px;
    // background-color: aqua;
    display: flex;
    justify-content: center;
    align-items: center;

    .shopping-navbar-inner {
        width: 80%;
        // background-color: chartreuse;
        display: flex;
        justify-content: space-evenly;
        align-items: center;

        .shopping-navbar-items {
            // background-color: crimson;
            padding: 30px 10px;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            position: relative;

            // 連接條狀樣式
            &:nth-of-type(1) .shopping-navbar-round::after,
            &:nth-of-type(2) .shopping-navbar-round::after {
                content: '';
                position: absolute;
                top: 50%;
                left: 105%;
                width: 510%;
                height: 2px;
                background-color: #b2bec3;
                // border-top: 1px solid #b2bec3;
                // border-bottom: 1px solid #b2bec3;
                transform: translateY(-50%);
                transition: all 0.3s ease-in-out;

                @include media768 {
                    width: 400%;
                }
                @include media568 {
                    width: 350%;
                }
                @include media414 {
                    width: 250%;
                }
                @include media320 {
                    width: 200%;
                }
            }

            // 步驟圓形樣式
            .shopping-navbar-round {
                display: inline-block;
                width: 40px;
                height: 40px;
                background-color: #fff;
                border: 1px solid #b2bec3;
                border-radius: 50%;
                margin: 0 0 20px;
                position: relative;
                display: flex;
                justify-content: center;
                align-items: center;
                font-size: 1rem;
                font-weight: bolder;
                user-select: none;
                transition: all 0.3s ease-in-out;
            }

            // 控制圓形背景樣式
            .round-color {
                background-color: #00b894;
            }

            // 控制圓形右邊橫條的顏色樣式
            &:nth-of-type(1) .round-active::after,
            &:nth-of-type(2) .round-active::after {
                content: '';
                position: absolute;
                top: 50%;
                left: 105%;
                width: 510%;
                height: 2px;
                background-color: #00b894;
                // border-top: 1px solid #b2bec3;
                // border-bottom: 1px solid #b2bec3;
                transform: translateY(-50%);

                @include media768 {
                    width: 400%;
                }
                @include media568 {
                    width: 350%;
                }
                @include media414 {
                    width: 250%;
                }
                @include media320 {
                    width: 200%;
                }
            }

            // 文字樣式
            .shopping-navbar-text {
                font-size: 1.1rem;
                font-weight: bolder;
                transition: all 0.3s ease-in-out;

                @include media320 {
                    font-size: 1rem;
                }
                &:hover,
                &:active {
                    color: #00b894 !important;
                }
            }
        }
    }
}
