.home-newProduct-container {
    width: 100%;
    margin: 100px auto 0;
    // background: rgba(0, 0, 0, 0.7);
    background: #111414;
    height: auto;

    // 標題名稱
    .home-newProduct-title {
        width: 100%;
        margin: 0 auto;
        text-align: center;
        font-size: 2.5rem;
        font-weight: bolder;
        color: #fff;
        padding: 40px 0 0;

        @include media768 {
            width: 100%;
            font-size: 2rem;
        }

        @include media320 {
            font-size: 1.8rem;
        }
    }

    // 副標題
    .home-newProduct-subtitle {
        width: 100%;
        text-align: center;
        font-size: 2rem;
        font-weight: bolder;
        color: #00b894;
    }

    // 整個輪播區塊大小
    .swiper {
        padding: 40px 0 100px;
        width: 100%;
        height: 100%;

        @media (min-width: 1141px) {
            width: 1140px;
        }
    }

    // skeleton 樣式
    .home-newProduct-items-skeleton {
        width: 220px;
        height: 350px;
        // margin-right: 20px;
    }
    // 每個輪播大小
    .home-newProduct-items {
        width: 240px;
        height: 350px;
        position: relative;
        transition: all 0.5s ease-out;
        background-color: #000;

        &::after {
            content: '點擊查看詳情';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            // background-color: #00b894;
            opacity: 0;
            display: flex;
            justify-content: center;
            align-items: center;
            text-align: center;
            font-size: 2rem;
            font-weight: bolder;
            transition: all 0.5s ease-out;
        }
        &:hover::after {
            opacity: 1;
            backdrop-filter: blur(5px);
        }
        &:hover .home-newProduct-btn {
            display: block;
        }
    }
    // 輪播中的按鈕+標題文字
    .home-newProduct-btn {
        // display: none;
        position: absolute;
        bottom: 0;
        left: 50%;
        width: 100%;
        padding: 20px 0;
        transform: translateX(-50%);
        text-align: center;
        background-color: rgba(0, 0, 0, 0.3);
        transition: all 0.5s ease-out;
        backdrop-filter: blur(5px);
        color: #fff;
        font-size: 1.2rem;
        font-weight: bolder;
        text-shadow: 2px 2px 10px #000;

        @include media768 {
            font-size: 1.15rem;
        }
        @include media375 {
            font-size: 0.9rem;
        }
    }
    // 商品名稱下方介紹
    .home-newProduct-small {
        font-size: 1rem;
    }

    // 輪播中的圖片
    .home-newProduct-img {
        display: block;
        max-width: 100%;
        width: 100%;
        height: 100%;
        max-height: 100%;
        object-fit: cover;
        object-position: center;
        border-radius: 5px;
        transition: all 0.3s ease-in;
        filter: brightness(0.9);

        &:hover {
            filter: brightness(1);
        }
    }

    // 客製化下方滾條樣式
    .swiper-scrollbar.swiper-scrollbar-horizontal {
        bottom: 17%;
        height: 5px;
    }
    .swiper-scrollbar-drag {
        background-color: #00b894;
    }
}
