.home-newInfo-container {
    margin: 100px auto 0;
    width: 100%;
    // padding: 150px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    @include media414 {
        margin: 50px auto 0;
    }

    // 主標題設置
    .home-newInfo-title {
        text-align: center;
        width: 100%;
        font-size: 2.5rem;
        font-weight: bolder;

        @include media768 {
            width: 100%;
            font-size: 2rem;
        }

        @include media320 {
            font-size: 1.8rem;
        }
    }

    // 副標題設置
    .home-newInfo-subtitle {
        font-size: 2rem;
        font-weight: bolder;
        color: #00b894;
    }

    // 設定 skeleton 讀取樣式
    .home-newInfo-table-skeleton-td {
        width: 100%;
        height: 100%;
    }
    .home-newInfo-table-skeleton-link {
        width: 100%;
        height: 100%;
    }
    // table 表格內容
    .home-newInfo-table {
        margin: 30px 0 0;
        font-size: 1.2rem;
        font-weight: bolder;
        width: 60%;

        @include media768 {
            width: 90%;
        }
        @include media568 {
            width: 90%;
            font-size: 1rem;
        }
        @include media375 {
            width: 100%;
        }

        // @include media320 {
        //     font-size: 1.8rem;
        // }

        tr {
            border-bottom: 2px solid #00b894;
            cursor: pointer;
            transition: color 0.2s ease;

            &:hover {
                color: #00b894;
            }

            td {
                padding: 10px 15px;
                text-align: left;
                transition: color 0.2s ease;

                @include media568 {
                    display: block;
                    padding: 2px 5px;
                    &:nth-child(1) {
                        text-align: center;
                    }

                    &:nth-child(2) {
                        // 限制單行出現長度
                        max-width: 93vw;
                        overflow: hidden;
                        white-space: nowrap;
                        text-overflow: ellipsis;
                    }
                }

                &:hover {
                    color: #00b894 !important;
                }
            }
        }
    }
}
