.home-brand-container {
    margin: 100px auto 0;
    max-width: 100%;
    width: 100%;
    text-align: center;

    // 主標題
    .home-brand-title {
        font-size: 2.5rem;
        font-weight: bolder;

        @include media768 {
            width: 100%;
            font-size: 2rem;
        }

        @include media320 {
            font-size: 1.8rem;
        }
    }

    // 副標題
    .home-brand-subtitle {
        font-size: 2rem;
        font-weight: bolder;
        color: #00b894;
        padding: 0 0 20px;
    }

    // skeleton 讀取樣式
    .home-brand-items-skeleton {
        width: 100%;
        height: 100%;
    }

    // 每個輪播區塊設定
    .home-brand-items {
        width: 10px;
        height: 100px;
        border-left: 2px solid black;
        border-top: 2px solid black;
        border-bottom: 2px solid black;
        // cursor: pointer;

        // 各品牌LOGO設定
        .home-brand-img {
            padding: 5px;
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center;
            background-color: #fff;
            // filter: brightness(1.1);
        }
    }
}
